

import './_components';


const lang = document.querySelector('.header__lang')
const langButton = document.querySelector('.header__lang-btn')
const langTitle = document.querySelector('.header__lang-title')
const langList = document.querySelector('.header__lang-list')
const langItem = document.querySelectorAll('.header__lang-item')

if (langButton) {
  langItem.forEach(item => {
    item.addEventListener('click', function () {
      langTitle.textContent = this.textContent
      langButton.focus()
      hiddenContactTypeList(langList, langButton)
    })
  });

  langButton.addEventListener('click', e => {
    lang.classList.toggle('active')
    langList.classList.toggle('open')
    langButton.classList.toggle('rotate')
  })
}


function hiddenContactTypeList(langList, langButton) {
  langList.classList.remove('open')
  langButton.classList.remove('rotate')
  lang.classList.toggle('active')
};


const userId = document.querySelector('.user__id')
if (userId){
  userId.textContent = userId.textContent.slice(0, 8) + '...' + userId.textContent.slice(-3)
}



function menuInit() {
  if (document.querySelector(".icon-menu")) {
    document.addEventListener("click", function (e) {
      if (e.target.closest('.icon-menu')) {
        document.documentElement.classList.toggle("lock");
        document.documentElement.classList.toggle("menu-open");
      }
      if (e.target.closest('.nav__link') && document.documentElement.classList.contains("lock")) {
        document.documentElement.classList.remove("lock");
        document.documentElement.classList.remove("menu-open");
      }
    });
  };
}
menuInit()


const btnShowPassword = document.querySelectorAll('.input__icon-btn')

btnShowPassword.forEach(btn => {
  btn.addEventListener('click', function (e) {
    e.preventDefault()
    this.classList.toggle('show')
    if (this.classList.contains('show')) {
      this.parentElement.querySelector('.input__control').type = "text"
      this.parentElement.querySelector('.input__control').focus()
    } else {
      this.parentElement.querySelector('.input__control').type = "password"
    }
  })
})

const switchBtn = document.querySelector('.switch-btn')

if (switchBtn) {
  switchBtn.addEventListener('click', function () {
    this.classList.toggle('switch-on')
  })

}

